@use '../base' as *;

.formulario {
    padding-top: 2rem;
    margin:3rem 0;

    p {
        font-size: 1.6rem;
        color: $grisOscuro;
        margin: 2rem 0 0 0;
    }

    legend {
        font-size: 2rem;
        color: $grisOscuro;
    }

    label {
        font-weight: bold;
        text-transform: uppercase;
        display: block;
    }

    input:not([type="submit"]),
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $blanco;
        border: 1px solid $grisOscuro;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }

    textarea {
        height: 15rem;
    }

    .formulario-radio {
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    input[type="radio"] {
        margin: 0;
        width: auto;
    }
}